export default {
	
	props: {
		url: ''
	},
	data() {
		return {
			lock: false, // 锁
			status: 1, // 1暂停 2播放
			currentTime: 0,  //当前进度
			duration: 100, // 总进度
			videoContext: ''
		}
	},
	computed:{
		timer() {
			return calcTimer(this.currentTime)
		},
		overTimer() {
			return calcTimer(this.duration)
		}
	},
	created() {
		 this.videoContext = uni.createVideoContext('myVideo')
	},
	mounted() {
	},
	methods: {
		// 倍速
		setRate(num) {
			this.videoContext.playbackRate(num)
		},
		
		// 播放
		play() {
			this.status = 2
			this.videoContext.play()
		},
		
		// 暂停
		stop() {
			this.videoContext.pause()
			this.status = 1
		},
		
		// 更新进度条
		timeupdate(event) {
			if(this.lock) return; // 锁
			var currentTime,duration;
			if(event.detail.detail) {
				currentTime = event.detail.detail.currentTime
				duration = event.detail.detail.duration
			}else {
				currentTime = event.detail.currentTime
				duration = event.detail.duration
			}
			this.currentTime = currentTime
			this.duration = duration
		},
		
		// 拖动进度条
		sliderChange(data) {
			this.videoContext.seek(data.detail.value)
		},
		
		//拖动中
		sliderChanging(data) {
			this.currentTime = data.detail.value
		},
		
		// 视频加载完成
		loadedmetadata(data) {
			this.duration = data.detail.duration
		}
	}
}

function calcTimer(timer) {
	if(timer === 0 || typeof timer !== 'number') {
		return '00:00'
	}
	let mm = Math.floor(timer / 60)
	let ss = Math.floor(timer % 60)
	if(mm < 10) {
		mm = '0' + mm
	}
	if(ss < 10) {
		ss = '0' + ss
	}
	return mm + ':' + ss
}

